// ---------------------------------------------------------


  const leverage = 50; // Ensure this matches server-side leverage
  const calculatePnL = (entryPrice, currentPrice, wager, direction) => {
    const priceChange = (currentPrice - entryPrice) / entryPrice;
    const pnl = wager * leverage * priceChange * (direction === 'up' ? 1 : -1);
    return pnl;
  };

