    {' ━━━  '}
    {isTradeClosed ? new Date(voteEntry.dateClosed).toLocaleDateString(undefined, { month: 'numeric', day: 'numeric' }) + ' ' + new Date(voteEntry.dateClosed).toLocaleTimeString(undefined, { hour: 'numeric', minute: 'numeric', hour12: true }).toLowerCase() : 'Open'}
